import uniList from "@/components/uni-ui/uni-list/uni-list.vue"
import uniListItem from "@/components/uni-ui/uni-list-item/uni-list-item.vue"
import myRequestGet from "../../utils/zgrequest.js"
export default {
	data() {
		return {
			books: []
		}
	},
	onLoad() {
		this.getBooks()
		
	},
	methods: {
		async getBooks() {
			let result = await myRequestGet("/api/wechat/bookshelf/builtin?sex=-1&categories=1&p2=129003")
			this.books = result.bookInfo;
		},
		goBooks(item){
			uni.navigateTo({
				url:`/pages/bookshelfs/bookshelfs?id=${item.bookId}`
				
			})
		}
		
		
	},
	components: { 
		uniList,
		uniListItem

	},

}
